import React from 'react'
import imgss from '../img/React.png'
import python from '../img/python.png'
import nodess from '../img/Nodejs.png'
import './Techstack.scss'
export default function Techstack() {
  const tech=[{
     name:'Reactjs',
     img:imgss,


  },{
    name:'Python',
    img:python

  }
  ,{
    name:'Nodejs',
    img:nodess
  }
]
  return (
    <div className='Techstack' id='#techstack'>
       <h1>TECHNOLOGY I WORK WITH</h1>
     <div className='Techh'>
     { tech.map((tech)=>{
       return (
         <div className='Elem'>
        
         
         <img className='IMGS' src={tech.img}/>
         <h1>{tech.name}</h1>
         </div>
       )

     })

     }
      
      </div>
      </div>
  )
}